/**
 * RISIVO CRM - Demo Forgot Password API
 * 
 * Sends password reset email to demo users
 */

import { NextResponse } from 'next/server'
import { prisma } from '@/lib/db'
import crypto from 'crypto'

export const runtime = 'nodejs'
export const dynamic = 'force-dynamic'

// Generate a secure random token
function generateToken(): string {
  return crypto.randomBytes(32).toString('hex')
}

// Get dynamic copyright year
function getCurrentYear(): number {
  return new Date().getFullYear()
}

// Generate password reset email HTML
function getPasswordResetEmailHtml(name: string, resetLink: string): string {
  return `
    <!DOCTYPE html>
    <html>
    <head>
      <meta charset="utf-8">
      <meta name="viewport" content="width=device-width, initial-scale=1.0">
    </head>
    <body style="font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif; line-height: 1.6; color: #333; max-width: 600px; margin: 0 auto; padding: 20px;">
      <div style="text-align: center; margin-bottom: 30px;">
        <img src="https://app.risivo.com/risivo-logo-color.png" alt="RISIVO CRM" style="height: 40px;">
      </div>
      
      <h1 style="color: #1a1a1a; font-size: 24px; margin-bottom: 20px;">Reset Your Password</h1>
      
      <p style="margin-bottom: 20px;">Hi ${name},</p>
      
      <p style="margin-bottom: 20px;">We received a request to reset your password for your RISIVO CRM demo account. Click the button below to set a new password:</p>
      
      <div style="text-align: center; margin: 30px 0;">
        <a href="${resetLink}" style="display: inline-block; background-color: #2563eb; color: white; padding: 14px 28px; text-decoration: none; border-radius: 8px; font-weight: 600;">Reset Password</a>
      </div>
      
      <p style="color: #666; font-size: 14px;">This link will expire in 1 hour. If you didn't request a password reset, you can safely ignore this email.</p>
      
      <p style="color: #666; font-size: 14px;">If the button doesn't work, copy and paste this link into your browser:</p>
      <p style="color: #2563eb; font-size: 12px; word-break: break-all;">${resetLink}</p>
      
      <div style="margin-top: 30px; padding-top: 20px; border-top: 1px solid #e9ecef;">
        <p style="color: #999; font-size: 12px; text-align: center; margin: 0 0 10px 0;">
          <a href="https://risivo.com/terms-of-service" style="color: #666; text-decoration: none;">Terms of Service</a>
          &nbsp;|&nbsp;
          <a href="https://risivo.com/privacy-policy" style="color: #666; text-decoration: none;">Privacy Policy</a>
          &nbsp;|&nbsp;
          <a href="mailto:support@risivo.com" style="color: #666; text-decoration: none;">Contact Support</a>
        </p>
        <p style="color: #999; font-size: 11px; text-align: center; margin: 0;">
          © ${getCurrentYear()} Risivo™ by Velocity Automation Corp. All rights reserved.
        </p>
      </div>
    </body>
    </html>
  `
}

// Send password reset email via SendGrid
async function sendPasswordResetEmail(email: string, name: string, token: string): Promise<boolean> {
  const baseUrl = process.env.NEXT_PUBLIC_APP_URL || 'https://app.risivo.com'
  const resetLink = `${baseUrl}/demo/reset-password?token=${token}`
  
  const sendgridApiKey = process.env.SENDGRID_API_KEY
  
  if (!sendgridApiKey) {
    console.log('========================================')
    console.log('PASSWORD RESET EMAIL (No email service configured)')
    console.log('To:', email)
    console.log('Name:', name)
    console.log('Reset Link:', resetLink)
    console.log('========================================')
    return true
  }
  
  try {
    const response = await fetch('https://api.sendgrid.com/v3/mail/send', {
      method: 'POST',
      headers: {
        'Authorization': `Bearer ${sendgridApiKey}`,
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        personalizations: [
          {
            to: [{ email, name }],
            subject: 'Reset Your RISIVO CRM Demo Password',
          },
        ],
        from: {
          email: process.env.SENDGRID_FROM_EMAIL || 'hello@risivo.com',
          name: process.env.SENDGRID_FROM_NAME || 'RISIVO CRM',
        },
        content: [
          {
            type: 'text/html',
            value: getPasswordResetEmailHtml(name, resetLink),
          },
        ],
        tracking_settings: {
          click_tracking: { enable: false },
          open_tracking: { enable: false },
        },
      }),
    })
    
    if (!response.ok) {
      const errorText = await response.text()
      console.error('[Forgot Password] Failed to send email via SendGrid:', response.status, errorText)
      return false
    }
    
    console.log('[Forgot Password] Reset email sent successfully to:', email)
    return true
  } catch (error) {
    console.error('[Forgot Password] Error sending email:', error)
    return false
  }
}

export async function POST(request: Request) {
  console.log('[Demo Forgot Password] Request received')
  
  try {
    const body = await request.json()
    const { email } = body

    if (!email) {
      return NextResponse.json(
        { success: false, error: 'Email is required' },
        { status: 400 }
      )
    }

    const normalizedEmail = email.toLowerCase().trim()

    if (!prisma) {
      return NextResponse.json(
        { success: false, error: 'Database not available' },
        { status: 503 }
      )
    }

    // Find the demo access
    const demoAccess = await prisma.demoAccess.findUnique({
      where: { email: normalizedEmail },
    })

    // Always return success to prevent email enumeration attacks
    // But only actually send email if account exists and is verified
    if (demoAccess && demoAccess.emailVerified && demoAccess.password) {
      // Generate password reset token
      const resetToken = generateToken()
      
      // Save the token (expires in 1 hour)
      await prisma.demoAccess.update({
        where: { id: demoAccess.id },
        data: {
          passwordResetToken: resetToken,
          passwordResetSentAt: new Date(),
        },
      })
      
      // Send the email
      await sendPasswordResetEmail(normalizedEmail, demoAccess.name, resetToken)
    } else {
      console.log('[Forgot Password] Account not found or not verified:', normalizedEmail)
    }

    // Always return success message
    return NextResponse.json({
      success: true,
      message: 'If an account exists with this email, you will receive a password reset link shortly.',
    })

  } catch (error) {
    console.error('[Demo Forgot Password] Error:', error)
    return NextResponse.json(
      { success: false, error: 'Failed to process request. Please try again.' },
      { status: 500 }
    )
  }
}
