/**
 * RISIVO CRM - Database Connection (Prisma ORM v6.8.x)
 * 
 * Simple PrismaClient singleton pattern for serverless environments.
 * No adapter needed with Prisma 6.x.
 */

import { PrismaClient } from '@prisma/client'

// Global cache to prevent multiple instances during development hot-reloading
const globalForPrisma = globalThis as unknown as {
  prisma: PrismaClient | undefined
}

/**
 * Create a new Prisma Client instance
 */
function createPrismaClient(): PrismaClient {
  return new PrismaClient({
    log: process.env.NODE_ENV === 'development' 
      ? ['error', 'warn'] 
      : ['error'],
  })
}

// Export singleton Prisma client
export const prisma = globalForPrisma.prisma ?? createPrismaClient()

// Cache in development to avoid too many connections during hot-reloading
if (process.env.NODE_ENV !== 'production') {
  globalForPrisma.prisma = prisma
}

export default prisma
